import React, { Component } from 'react';
import { getAllProjects, getAllCategories, getByCategory } from '../services/Project';
import ProjectCard from './ProjectCard';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

class Home extends Component {

  constructor(props) {
    super(props)
    this.state = {
      projects: [],
      categories: [],
    }
  }

  componentDidMount() {
    const category = this.props.match.params.category;
    const projects = category ? getByCategory(category) : getAllProjects();
    const categories = getAllCategories();
    this.setState({
      projects: [...projects],
      categories: [...categories],
      category: category
    });
  }

  render() {
    const { projects, categories, category } = this.state;
    return (
      <center>
        <Typography variant="subheading">Browse by Category</Typography>
        <div style={{display: 'inline-flex', maxWidth: '90vw', overflow: 'scroll'}}>
          { categories.map((category, id) =>
            <Button
              key={id}
              href={`/category/${category}`}
              style={{ margin: '1rem 0.5rem' }}
              variant="contained"
              color="secondary"
              size="large" >
              { category }
            </Button>
          )}
        </div>
        <Typography visible={category} variant="headline">Projects for: {category}</Typography>
        { projects.map((project, id) =>
          <ProjectCard key={id} project={project} />
        )}
      </center>
    )
  }
}

export default Home



